.virtualBot {
    position: fixed;
    bottom: 10px;
    right: 10px;
    width: 200px;
    height: 200px;
    padding: 5px;
    border: none;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 291px;
    margin-bottom: 90px;
    overflow: hidden;
    background-color: #f9f9f9;
    border-radius: 10px;

}