/* 챗봇 전체 스타일 */
.chatBot {

  position: fixed;
  right: -20px;
  bottom: 100px;
}

/* 챗봇 스마일 애니메이션 & 음성인식 버튼 스타일 */
.botBody {

  position: fixed;
  right: 30px;
  display: flex;
  bottom: 20px;
}

/* 챗봇 보이스 UI 스마일 스타일 */
.offSmile {

  background-image: url('../media/smile.svg');
  width: 50px;
  height: 50px;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  border-Radius: 50%;
  border: none;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  animation: siri-swing 1s linear infinite;
  -webkit-animation: siri-swing 1s linear infinite; /* Safari and Chrome */
}

@keyframes siri-swing {

  0% {

    transform: rotate(25deg);
    animation-timing-function: cubic-bezier(0.455, 0.030, 0.515, 0.955);
    animation-duration: 0.5s;
  }

  50% {

    transform: rotate(-25deg);
    animation-timing-function: cubic-bezier(0.455, 0.030, 0.515, 0.955);
    animation-duration: 0.8s;
  }

  100% {

    transform: rotate(25deg);
    animation-timing-function: cubic-bezier(0.455, 0.030, 0.515, 0.955);
    animation-duration: 0.8s;
  }
}

/* 챗봇 텍스트 UI 스마일 스타일 */
.onSmile {

  background-image: url('../media/smile.svg');
  width: 50px;
  height: 50px;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  border-Radius: 50%;
  border: none;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  animation: siri-rotate 1s linear infinite;
  -webkit-animation: siri-rotate 1s linear infinite; /* Safari and Chrome */
}

@keyframes siri-rotate {

  0%  {

    transform: rotate(0deg);
    animation-timing-function: cubic-bezier(0.455, 0.030, 0.515, 0.955);
    animation-duration: 0.5s;
  }

  50% {

    transform: rotate(180deg);
    animation-timing-function: cubic-bezier(0.455, 0.030, 0.515, 0.955);
    animation-duration: 0.8s;
  }

  100%{

    transform: rotate(360deg);
    animation-timing-function: cubic-bezier(0.455, 0.030, 0.515, 0.955);
    animation-duration: 0.8s;
  }
}

button {

  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  margin-bottom: 5px;
  border: none;
  font-family: 'Roboto', sans-serif;
  font-size: 15px;
  cursor: pointer;
}