.chatLogUI {
  position: fixed;
  bottom: 100px;
  right: 30px;
  width: 270px;
  height: 500px;
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  z-index: 9999;
}

.chatHeader {
  background-color: #0084ff;
  color: white;
  font-size: 20px;
  font-weight: bold;
  padding: 10px;
  text-align: center;
}

.chatHeaderFoot {
  color: black;
  font-size: 11px;
  text-align: center;
}

.chatBody {
  display: flex;
  flex-direction: column;
  height: 90%;
}

.chatLog {
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  height: 100%;
  padding: 10px;
}

.chatLog::-webkit-scrollbar {
  width: 8px;  /* 스크롤바의 너비 */
}

.chatLog::-webkit-scrollbar-thumb {
  height: 30%; /* 스크롤바의 길이 */
  background: #0084ff; /* 스크롤바의 색상 */
  border-radius: 10px;
}

.chatLog::-webkit-scrollbar-track {
  background: rgba(33, 122, 244, .1);  /*스크롤바 뒷 배경 색상*/
}

.chatInput {
  display: flex;
  align-items: center;
  padding: 10px;
  margin-bottom: 10px;
}

.chatInput input[type="text"] {
  padding: 8px;
  border-radius: 20px;
  border: none;
  outline: none;
}

.chatInput button {
  margin-right: 10px;
  padding: 8px;
  border-radius: 20px;
  border: none;
  background-color: #0084ff;
  color: #fff;
  cursor: pointer;
  font-size: 12px;
}

.userBubble {

  align-self: flex-end;
  background-color: #F5F5F5;
  border-radius: 10px;
  padding: 10px;
  margin-bottom: 35px;
}

.botBubble {
  align-self: flex-start;
  background-color: #E5E5EA;
  border-radius: 10px;
  padding: 10px;
  margin-bottom: 35px;
}

